.effect {
  display: flex;
  flex-direction: column;
}

.effect-titleDiv {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 5px 5px;
}

.effect-title {
}
.effect-remove {
  width: 15px;
  height: 15px;
  background-image: url(data:image/png;base64,);
  cursor: pointer;
  border: 1px solid #dddd;
  border-radius: 100%;
  background-size: 11px;
  background-repeat: no-repeat;
  background-position: center;
  padding: 3px;
}
.effect-remove:hover {
  border: 1px solid red;
}
.effect-properties {
  padding-left: 18px;
  display: flex;
  flex-direction: column;
}
.effect-addModuleDiv {
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
  cursor: pointer;
}
.effect-addModule {
  height: 18px;
  width: 18px;
  background-image: url(data:image/png;base64,);
  background-size: contain;
  margin-right: 4px;
  background-size: 18px;
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
}
