.burstListProperty-div {
  display: flex;
  flex-direction: column;
  align-items: baseline !important;
  height: unset !important;
}
.burstListProperty-nested-div {
  padding-left: 18px;
  width: calc(100% - 18px);
  position: relative;
}
.burstListProperty-table {
  width: 100%;
  font-size: 10px;
  text-align: left;
}
.burstListProperty-number-field {
  width: 100%;
  font-size: inherit;
}
.burstListProperty-add-burst {
  height: 14px;
  width: 14px;
  background-image: url(../../../assets/round-add-button.png);
  background-size: contain;
  margin-right: 4px;
}
.burstListProperty-remove-burst {
  height: 14px;
  width: 14px;
  background-image: url(data:image/png;base64,);
  border: 1px solid #dddd;
  border-radius: 100%;
  background-size: 8px;
  background-repeat: no-repeat;
  background-position: center;
  padding: 0px;
}
.burstListProperty-remove-burst:hover {
  border: 1px solid red;
}
