.module {
  display: flex;
  flex-direction: column;
}
.module-title-layout {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.module-typeDropdown {
  /* width: calc(200px - 8px - 18px - 8px - 18px); */
  height: 100%;
  width: 88%;
  cursor: pointer;
}
.module-moveContainer {
  display: flex;
  flex-direction: column;
}
.module-move {
  width: 18px;
  height: 18px;
  background-size: 8px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  border-radius: 100%;
}
.module-move:hover {
  background-color: gray;
}
.module-moveUp {
  background-image: url(../../assets/arrow-down.png);
  transform: rotate(180deg);
}
.module-moveDown {
  background-image: url(../../assets/arrow-down.png);
}
.module-remove {
  width: 18px;
  height: 18px;
  background-image: url(data:image/png;base64,);
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #ddd;
  background-size: 10px;
  border-radius: 100%;
  padding: 1px;
}
.module-remove:hover {
  border: 1px solid red;
}
.module-properties {
  padding-left: 18px;
  display: flex;
  flex-direction: column;
}
select:focus-visible {
  outline-offset: 0px;
  outline: none;
}
*:focus {
  outline: none;
}
input:focus {
  outline: none;
}
